import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { Button, Header, Icon, Segment, SegmentInline } from "semantic-ui-react";
import agent from "../../app/api/agent";
import userQuery from "../../app/common/util/hooks";
import { useStore } from "../../app/stores/store";
import LoginForm from "./LoginForm";

export default function ConfirmEmail() {
    const {modalStore}  = useStore();

    const email = userQuery().get('email') as string;
    const token = userQuery().get('token') as string;

    const Status = {
        Verifying: 'Verifying',
        Failed: 'Failed',
        Success: 'Success'
    }

    const [status, setStatus] = useState(Status.Verifying);

    function handleConfirmEmailResend() {
        agent.Account.resendEmailConfirm(email).then( () => {
            toast.success('Verification eamil resent - please check you email');            
        }).catch(error=>console.log(error));
    }

    useEffect(()=>{
        agent.Account.verifyEmail(token, email)    .then(()=>{
            setStatus(Status.Success)
        }).catch(()=>{
            setStatus(Status.Failed)
        })
    }, [Status.Failed, Status.Success, agent])

    function getBody() {
        switch(status) {
            case Status.Verifying:
                return <p>Verifying...</p>
            case Status.Failed:
                return (
                    <div>
                        <p>Verification failed. You can try resending the verify link to your email</p>
                        <Button primary onClick={handleConfirmEmailResend} size='huge' content='Resend email' />
                    </div>
                )
            case Status.Success:
                return (
                    <div>
                        <p>Email has been verified - you can now login</p>
                        <Button primary onClick={()=> modalStore.openModal(<LoginForm/>)} size='huge' />
                    </div>
                )                  
        }
    }

    return (
        <Segment placehgolder textAlign='center'>
            <Header icon>
                <Icon name='envelope'/>
                    Email Verification
            </Header>
            <Segment.Inline>
                {getBody()}
            </Segment.Inline>
        </Segment>
    )

}